.nav-bar-container {
	width: 100%;
	height: 60px;
	top: 0;
	z-index: 3;
	position: sticky;
	display: inline-block;
	background-color: #1f1f23;
}

.nav-bar-button {
	width: 85px;
	height: 40%;
	float: right;
	margin-left: 5px;

	position: relative;
	top: 50%;
	transform: translateY(-50%);

	font-size: 18px;
	text-align: center;
	vertical-align: middle;
	color: blanchedalmond;

	-youbkit-touch-callout: none;	/* iOS Safari */
	-youbkit-user-select: none;		/* Chrome 6.0+, Safari 3.1+, Edge & Opera 15+ */
	-moz-user-select: none;			/* Firefox */
	-ms-user-select: none;			/* IE 10+ and Edge */
	user-select: none;				/* Non-prefixed version, currently supported by Chrome and Opera */
}

.nav-bar-button:hover {
	cursor: pointer;
	color: #fff;
	text-shadow:0 0 5px #fff, 
				0 0 10px #fff, 
				0 0 20px #fff, 
				0 0 40px #0ff, 
				0 0 80px #0ff, 
				0 0 90px #0ff, 
				0 0 100px #0ff, 
				0 0 150px #0ff;
}

.drink-logo {
	width: 200px;
	height: 70%;
	display: inline-block;
	position: relative;
	top: 45%;
	transform: translateY(-50%);
	margin: 0px;
	font-size: 30px;
	font-weight: bolder;
	text-align: center;
	color: rgb(255, 251, 245);
	-youbkit-touch-callout: none;	/* iOS Safari */
	-youbkit-user-select: none;		/* Chrome 6.0+, Safari 3.1+, Edge & Opera 15+ */
	-moz-user-select: none;			/* Firefox */
	-ms-user-select: none;			/* IE 10+ and Edge */
	user-select: none;				/* Non-prefixed version, currently supported by Chrome and Opera */
}

.search-box-wrapper {
	width: 250px;
	height: 24px;

	float: right;
	position: relative;
	top: 50%;
	transform: translateY(-50%);

	border-radius: 3px 0px 0px 3px;
	background-color: transparent;
}

.search-box-wrapper .search-box {
	width: 85%;
	height: 100%;
	position: relative;
	outline: 0px;
	padding: 0px;
	padding-left: 10px;
	box-sizing: border-box;
	border: transparent;
	margin-right: 2px;
	border-radius: 4px 0px 0px 4px;
	background-color: #46464d;
}

.search-box-wrapper .search-icon-wrapper {
	width: 10%;
	height: 100%;
	position: relative;
	display: inline-block;
	border-radius: 0px 4px 4px 0px;
	top: 50%;
	transform: translateY(-50%);
	background-color: #37373b;
}

.search-box-wrapper .search-icon-wrapper .search-icon {
	width: 20px;
	margin: 0px;
	position: relative;

	bottom: 1px;
	left: 50%;
	transform: translateX(-50%);
}

.search-icon-wrapper:hover {
	cursor: pointer;
}

.search-box::placeholder {
	color: rgb(160, 155, 155);
}

.search-box:focus {
	background-color: rgb(85, 85, 85);
}

.search-box:focus::placeholder {
	color: rgb(194, 192, 186);
}

.nav-profile-pic {
	width: 30px;
	height: 30px;
	float: right;

	margin-left: 5px;
	margin-right: 5px;

	position: relative;
	top: 50%;
	transform: translateY(-50%);
	border-radius: 100%;
}
